.catalog {
  width: 100%;
}

.catalog__container {
  display: flex;
  flex-direction: column;
  gap: 32px;
}

.catalog-list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  align-items: stretch;
  gap: 60px 30px;
}

.catalog-item{
  display: flex;
  flex-direction: column;
}

.catalog-item__img {
  width: 100%;
  aspect-ratio: 1/1;
  padding-bottom: 10px;
  transition: 0.25s;


  &:hover {
    padding-top: 10px;
    padding-bottom: 0;
  }
}

.catalog-item__text {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.catalog-item__title {
  font-size: 20px;
  line-height: 24px;
  font-weight: 500;
}

.catalog-item__description {
  font-size: 14px;
  line-height: 20px;
  font-weight: 400;
}


.catalog-item__buy-block {
  margin-top: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.catalog-item__cost {
  font-size: 20px;
  font-weight: 600;
  line-height: 22px;
}

.catalog-item__buy-btn {
  padding: 8px 20px;
  font-size: 16px;
  line-height: 24px;
  font-weight: 600;
  color: var(--second-color);
  background-color: var(--second-color-alpha);
  border-radius: 100px;
}
